.menu {
  width: 100%;
  height: 100vh;
  position: absolute;
  /* filter: grayscale(100%); ;*/
  top: 0;
  left: 0;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.9);
  background-image: linear-gradient( rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url(../background/Gaia_11.jpg);
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: fadein 0.1s linear;
  color: #fff;
  user-select: none;
}

.menu-container {
  pointer-events: all;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  animation: fadein 0.2s linear;
  width: 80vmin;
  /* height: 105vmin; */
}

.menu-container p {
  font-family: "AgencyFB", sans-serif;
  padding-top: 0;
  line-height: normal;
  font-size: 9vmin;
  padding: 0 0 5vmin 0;
}

.menu-button {
  font-family: "AgencyFB", sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  width: 75vmin;
  padding: 3vmin;
  font-size: 8.5vmin;
  margin: 3vmin 0 3vmin 0;
}

.menu-button.b1 {
  animation: fadein 0.4s linear;
  /* background-color: rgba(0, 0, 0, 1);; */
}

.menu-button.b2 {
  animation: fadein 0.6s linear;
}

.menu-button.b3 {
  animation: fadein 0.9s linear;
}

.menu-button.b4 {
  animation: fadein 1s linear;
}

.menu-button.b5 {
  animation: fadein 1.2s linear;
}

.menu-social {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* animation: fadein 1.2s linear; */
  width: 75vmin;
  padding-top: 4vmin;
}

.menu-social img {
  width: 6.7vmin;
}

@media screen and (min-width: 300px) And (max-height: 890px) And (min-aspect-ratio: 9/16) {
  .menu-container {
    width: 44.60vmax;
    /* height: 58.54vmax;; */
  }
  .menu-container p {
    font-size: 5.02vmax;
    padding: 0 0 2.79vmax 0;
  }
  .menu-button {
    width: 41.81vmax;
    padding: 1.67vmax;
    font-size: 4.74vmax;
    margin: 1.67vmax 0 1.67vmax 0;
  }
  .menu-social {
    width: 41.81vmax;
    padding-top: 2.23vmax;
  }
  .menu-social img {
    width: 3.74vmax;
  }
}

@media screen and (min-width: 300px) And (max-height: 890px) And (min-aspect-ratio: 1/1) {
  .menu-container {
    width: 44.60vmin;
    /* height: 58.54vmin;; */
  }
  .menu-container p {
    font-size: 5.02vmin;
    padding: 0 0 2.79vmin 0;
  }
  .menu-button {
    width: 41.81vmin;
    padding: 1.67vmin;
    font-size: 4.74vmin;
    margin: 1.67vmin 0 1.67vmin 0;
  }
  .menu-social {
    width: 41.81vmin;
    padding-top: 2.23vmin;
  }
  .menu-social img {
    width: 3.74vmin;
  }
}

@media screen and (min-width: 500px) And (min-height: 891px) {
  .menu-container {
    width: 400px;
    /* height: 525px;; */
  }
  .menu-container p {
    font-size: 45px;
    padding: 0 0 25px 0;
  }
  .menu-button {
    width: 375px;
    padding: 15px;
    font-size: 42.5px;
    margin: 15px 0 15px 0;
  }
  .menu-social {
    width: 375px;
    padding-top: 20px;
  }
  .menu-social img {
    width: 33.5px;
  }
}